import Vue from 'vue';
import Component from 'vue-class-component';
import { Action, State } from 'vuex-class';
import checkbox from 'muse-ui/checkbox';

import { ITask } from '~/entity/task/index';
import { IPayParams } from '~/entity/route_params';
import Api from '~/api';
import { TASK_REGION, APP_ID } from '~/config';
import { ToastMsg } from '~/entity/toast';
import Utils from '~/utils';
import { CACHE_NAME } from '~/cache';

import bus from '~/bus/index'; // 引入bus事件存储组建

@Component({
    components: {
        'mu-checkbox': checkbox
    }
})
export default class PaymentComp extends Vue {
    
    task: ITask = null;

    taskRegion = TASK_REGION;

    payTipShow = false; // 控制支付系统任务支付完成是否弹出提示
    neverNotifyCheck = false; // 控制是否不再显示对话框

    @Action('showToast')
    showToast;

    params: IPayParams = null;

    async activated() {
        // const params: IPayParams = this['$route'].params;
        // const params: IPayParams = Utils.getItem(CACHE_NAME.payParams);
        const params: IPayParams = this.payParmas;
        this.params = params;

        const res = await Api.getPublishedTaskDetail(params.taskId);
        
        this.task = res.body.obj;
        const task = res.body.obj;
        // 获取cookies中的不再提醒值
        // this.neverNotifyCheck = this.getCookie();
        this.getNotifyCheckFromLocalstorage();

        if (task.taskType === 1) { // 定向任务
			const users = [];
			for (const subsetTask of task.subsetTasks) {
				if (subsetTask.toUser && subsetTask.toUser.realname) {
					users.push(subsetTask.toUser.realname);
				}
			}
			this.task['toUsers'] = users.join('、');
        }
    }

    async deactivated() {
        this.payTipShow = false;
    }

    get payParmas() {
        return bus.cacheData;
    }

    // getCookie() {
    //     const name = 'neverNotifyCheck=';
    //     if (document.cookie.length > 0) {
    //         let start = document.cookie.indexOf(name);
    //         if (start !== -1) {
    //             start = start + name.length + 1;
    //             let end = document.cookie.indexOf(';', start);
    //             if (end === -1) end = document.cookie.length;
    //             if (document.cookie.substring(start, end) === 'true') {
    //                 this.showToast(new ToastMsg('cookie is true'));
    //                 return true;
    //             } else {
    //                 this.showToast(new ToastMsg('cookie is false'));
    //                 this.showToast(document.cookie.substring(start, end));
    //                 return false;
    //             }
    //         }
    //     } else {
    //         this.showToast(new ToastMsg('cookie is null'));
    //         return false;
    //     }
    // }

    getNotifyCheckFromLocalstorage() {
        let notifyTemp = window.localStorage.getItem('neverNotifyCheck');
        if (notifyTemp) {
            this.neverNotifyCheck = notifyTemp === 'true';
        } else {
            this.neverNotifyCheck = false;
        }        
    }

    async pay() {
        const params: IPayParams = this.params;
        const res = await Api.pay(params.taskId);
        console.log(res.body.obj);
        
        const nonceStr = res.body.obj.nonceStr;
        const prepayId = res.body.obj.package;
        const signType = res.body.obj.signType;
        const paySign = res.body.obj.paySign;
        const timeStamp = res.body.obj.timeStamp;        
        
        // if (this.task.taskType === 3 && !this.neverNotifyCheck) { 
        //     this.payTipShow = true;                            
        // } else {
        //     // 跳转到订单列表页
        //     this['$router'].replace({
        //         name: 'myTaskList'
        //     });
        // }
        if (process.env.AILINK_ENV === 'wechat') {
            this.weChatPay(APP_ID, timeStamp, nonceStr, prepayId, signType, paySign);
        }
    }

    weChatPay(appId: string, timeStamp: string, nonceStr: string, prepayId: string, signType: string, paySign: string) {
        const that = this;
        function onBridgeReady() {

            // prepayId = 'prepay_id=' + prepayId;
            // const timeStamp = Date.parse(new Date().toString()).toString().substring(0, 10);
            // console.log('时间戳：' + timeStamp);
            // console.log('appId ' + appId);
            // console.log('nonceStr ' + nonceStr);
            // console.log('prepayId ' + prepayId);
            // console.log('signType ' + signType);
            // console.log('paySign ' + paySign);
            console.log('**********wx onBridgeReady*************');

            window['WeixinJSBridge'].invoke(
                'getBrandWCPayRequest', {
                    appId, // 公众号名称，由商户传入     
                    timeStamp, // 时间戳，自1970年以来的秒数     
                    nonceStr, // 随机串     
                    'package': prepayId,     
                    signType, // 微信签名方式
                    paySign // 微信签名 
                },
                (res) => {    
                    console.log(res.err_msg);                    
                    if (res.err_msg === 'get_brand_wcpay_request:ok' ) { 
                        that.showToast(new ToastMsg('支付成功'));                        
                        if (that.task.taskType === 3 && !that.neverNotifyCheck) { 
                            that.payTipShow = true;                            
                        } else {
                            // 跳转到订单列表页
                            that['$router'].replace({
                                name: 'myTaskList'
                            });
                        }
                    } else {
                        that.showToast(new ToastMsg('支付失败'));
                        // that.showToast(new ToastMsg(res.err_msg));
                    }
                }
            ); 
        }
        if (typeof window['WeixinJSBridge'] === undefined) {
            if ( document.addEventListener ) {
                document.addEventListener('WeixinJSBridgeReady', onBridgeReady, false);
            } else if (document['attachEvent']) {
                document['attachEvent']('WeixinJSBridgeReady', onBridgeReady); 
                document['attachEvent']('onWeixinJSBridgeReady', onBridgeReady);
            }
         } else {
            onBridgeReady();
        }
    }

    closeTip() {
        if (this.neverNotifyCheck) {
            window.localStorage.setItem('neverNotifyCheck', 'true');
            console.log(window.localStorage);
        } else {
            window.localStorage.setItem('neverNotifyCheck', 'false');
        }
        this.payTipShow = false;
        // 跳转到订单列表页
        this['$router'].replace({
            name: 'myTaskList'
        });
    }
}



